<template>
  <div ref="wrapper">
    <slot></slot>
  </div>
</template>
<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  import {mapState} from 'vuex'

  export default {
    props: {
      /**
       * 1 scroll
       * 2 scroll
       * 3 scrollswipescroll
       */
      /*
      * */
      scrollReset: {
        type: Boolean,
        default: true
      },
      probeType: {
        type: Number,
        default: 1
      },
      //
      bounce: {
        type: Boolean,
        default: false
      },
      //

      /**
       * click
       */
      click: {
        type: Boolean,
        default: true
      },
      /**
       * 
       */
      scrollX: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      listenScroll: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      data: {
        type: Array,
        default: null
      },
      /**
       * 
       */
      pullup: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      pulldown: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      beforeScroll: {
        type: Boolean,
        default: false
      },
      /**
       * scroll
       */
      refreshDelay: {
        type: Number,
        default: 20
      }
    },
    computed: {
      ...mapState(["generalizeYiPageY"])
    },
    mounted() {
      this.__boxheight(this.$refs.wrapper)
      // DOMbetter-scroll
      setTimeout(() => {
        this._initScroll()
      }, 20)
    },
    methods: {
      _initScroll() {
        if (!this.$refs.wrapper) {
          return
        }
        let obj = {
          probeType: this.probeType,
          click: this.click,
          scrollX: this.scrollX,
          bounce: this.bounce,
        }
        /*if(this.$route.name === "generalizeYiPage"){
          obj.startY = this.generalizeYiPageY
        }*/
        // better-scroll
        this.scroll = new BScroll(this.$refs.wrapper, obj)

        // 
        if (this.listenScroll) {
          this.scroll.on('scroll', (pos) => {
            this.$emit('scroll', pos)
          })
        }
        // 
        if (this.pullup) {
          this.scroll.on('scrollEnd', () => {
            let result = this.scroll && this.scroll.y
            console.log(this.$route.name)
            if (this.$route.name !== "generalizeErPage") {
              this.$store.commit("GENERALIZEYIPAGEY", {result})
            }
            // 
            if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
              this.$emit('scrollToEnd')
            }
          })
        }

        // 
        if (this.pulldown) {
          this.scroll.on('touchend', (pos) => {
            // 
            if (pos.y > 50) {
              this.$emit('pulldown')
            }
          })
        }

        // 
        if (this.beforeScroll) {
          this.scroll.on('beforeScrollStart', () => {
            this.$emit('beforeScroll')
          })
        }
      },
      disable() {
        // better-scrolldisable
        this.scroll && this.scroll.disable()
      },
      enable() {
        // better-scrollenable
        this.scroll && this.scroll.enable()
      },
      refresh() {
        // better-scrollrefresh
        this.scroll && this.scroll.refresh()
      },
      scrollTo() {
        // better-scrollscrollTo
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
      },
      scrollToElement() {
        // better-scrollscrollToElement
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
      }
    },
    updated() {
      if (this.scroll) {
        if(this.$route.name !== "generalizeErPage"){
          this.scrollTo(0, this.generalizeYiPageY)
        }
        this.refresh()
      } else {
        this.__boxheight(this.$refs.wrapper)
        // DOMbetter-scroll
        setTimeout(() => {
          this._initScroll()
        }, 20)
      }
    },
    watch: {
      // refreshDelayrefresh
      data() {
        setTimeout(() => {
          this.refresh()
        }, this.refreshDelay)
      },
      $route(to, form) {
        if (to.meta.keepAlive) {
          this.$store.commit("GENERALIZEYIPAGEY", {result: 0})
        }
        if (to.name === "productPage" || to.name === "creditCardPage") {
          this.refresh()
        }
      },
      scrollReset(val){
        this.$store.commit("GENERALIZEYIPAGEY", {result: 0})
      }
    }
  }
</script>



