//dom

//
export function hasClass(el, className) {
  let reg = new RegExp('(^|\\s)' + className + '(\\s|$)')//className 
  return reg.test(el.className)//
}
//
export function addClass(el, className) {
  if (hasClass(el, className)) {
    //
    return
  }
  let newClass
  if(el.className) {
    newClass= el.className.split(' ')    //
  }else{
    newClass = []
  }
  newClass.push(className)//
  el.className = newClass.join(' ')//
}
//
export function removeClass(el, className) {
  if (!hasClass(el, className)) {
    // 
    return
  }
  let reg = new RegExp('(^|\\s)' + className + '(\\s|$)', 'g')//g ;
  el.className = el.className.replace(reg, ' ') //
}
// 
export function getData(el, name, val) {
  //el.getAttributeNode()  Attr 
  // let prefix = 'data-'
  if (val) {
    return el.setAttribute(name, val)//val 
  }
  return el.getAttribute(name)// 
}
//
export function getRect(el) {
  if (el instanceof window.SVGElement) {//SVG
    //svg HTMLsvg  
    let rect = el.getBoundingClientRect() //Element.getBoundingClientRect()
    return {
      top: rect.top,
      left: rect.left,
      width: rect.width,
      height: rect.height
    }
  } else {
    // 
    return {
      top: el.offsetTop,
      left: el.offsetLeft,
      width: el.offsetWidth,
      height: el.offsetHeight
    }
  }
}



